angular.module('fairForms')
.config(function($stateProvider, $urlRouterProvider, $locationProvider) {
  // For any unmatched url, redirect to /state1
  $urlRouterProvider.otherwise(((checkSmall()) ? '/' : '/'));
  // Now set up the states
  $stateProvider
    .state('home', {
      url: '/',
      templateUrl: 'partials/home.html'
    })
    .state('downloads', {
      url: '/downloads',
      templateUrl: 'partials/downloads.html'
    })
    .state('form', {
      url: "/form",
      templateUrl: "partials/main.html",
      controller: "FormController"
    })
    .state('form.start', {
      url: "/start",
      templateUrl: "partials/start.html"
    })
    .state('form.dealerinfo', {
      url: "/dealer-info",
      templateUrl: "partials/dealerinfo.html"
    })
    .state('form.personnel', {
      url: "/personnel",
      templateUrl: "partials/personnel.html"
    })
    .state('form.producerinfo', {
      url: "/producer-info",
      templateUrl: "partials/producer-info.html"
    })
    .state('form.companystructure', {
      url: "/company-structure",
      templateUrl: "partials/companystructure.html"
    })
    .state('form.licenses', {
      url: "/licenses",
      templateUrl: "partials/licenses.html"
    })
    .state('form.confirmations', {
      url: "/email-confirmations",
      templateUrl: "partials/confirmations.html"
    })
    .state('form.finish', {
      url: "/finish",
      templateUrl: "partials/finish.html"
    })
    .state('form.full', {
      url: "/",
      templateUrl: "partials/full.html"
    });

    $locationProvider.html5Mode(true);
});
